/* Component containers
----------------------------------*/
.ui-widget {
  font-family: @font-family-base;
  font-size: @font-size-base;
}

.ui-widget .ui-widget {
  font-size: @font-size-base;
}

.ui-widget input,
.ui-widget select,
.ui-widget textarea,
.ui-widget button {
  font-family: @font-family-base;
  font-size: @font-size-base;
}

.ui-widget-content {
  border: 1px solid @panel-inner-border;
  background: @panel-bg;
  color: @text-color;
}

.ui-widget-content a {
  color: @link-color;
}

.ui-widget-header {
  border: 1px solid @panel-inner-border;
  background: @panel-default-heading-bg;
  color: @text-color;
  font-weight: bold;
}

.ui-widget-header a {
  color: @link-color;
}

/* Interaction states
----------------------------------*/
.ui-state-default,
.ui-widget-content .ui-state-default,
.ui-widget-header .ui-state-default {
  border: 1px solid #c5c5c5 /*{borderColorDefault}*/;
  background: #f6f6f6 /*{bgColorDefault}*/ /*{bgImgUrlDefault}*/ /*{bgDefaultXPos}*/ /*{bgDefaultYPos}*/ /*{bgDefaultRepeat}*/;
  font-weight: normal /*{fwDefault}*/;
  color: #454545 /*{fcDefault}*/;
}

.ui-state-default a,
.ui-state-default a:link,
.ui-state-default a:visited {
  color: #454545 /*{fcDefault}*/;
  text-decoration: none;
}

.ui-state-hover,
.ui-widget-content .ui-state-hover,
.ui-widget-header .ui-state-hover,
.ui-state-focus,
.ui-widget-content .ui-state-focus,
.ui-widget-header .ui-state-focus {
  border: 1px solid #cccccc /*{borderColorHover}*/;
  background: #ededed /*{bgColorHover}*/ /*{bgImgUrlHover}*/ /*{bgHoverXPos}*/ /*{bgHoverYPos}*/ /*{bgHoverRepeat}*/;
  font-weight: normal /*{fwDefault}*/;
  color: #2b2b2b /*{fcHover}*/;
}

.ui-state-hover a,
.ui-state-hover a:hover,
.ui-state-hover a:link,
.ui-state-hover a:visited,
.ui-state-focus a,
.ui-state-focus a:hover,
.ui-state-focus a:link,
.ui-state-focus a:visited {
  color: #2b2b2b /*{fcHover}*/;
  text-decoration: none;
}

.ui-state-active,
.ui-widget-content .ui-state-active,
.ui-widget-header .ui-state-active {
  border: 1px solid #003eff /*{borderColorActive}*/;
  background: #007fff /*{bgColorActive}*/ /*{bgImgUrlActive}*/ /*{bgActiveXPos}*/ /*{bgActiveYPos}*/ /*{bgActiveRepeat}*/;
  font-weight: normal /*{fwDefault}*/;
  color: #ffffff /*{fcActive}*/;
}

.ui-state-active a,
.ui-state-active a:link,
.ui-state-active a:visited {
  color: #ffffff /*{fcActive}*/;
  text-decoration: none;
}

/* Interaction Cues
----------------------------------*/
.ui-state-highlight,
.ui-widget-content .ui-state-highlight,
.ui-widget-header .ui-state-highlight {
  border: 1px solid #dad55e /*{borderColorHighlight}*/;
  background: #fffa90 /*{bgColorHighlight}*/ /*{bgImgUrlHighlight}*/ /*{bgHighlightXPos}*/ /*{bgHighlightYPos}*/ /*{bgHighlightRepeat}*/;
  color: #777620 /*{fcHighlight}*/;
}

.ui-state-highlight a,
.ui-widget-content .ui-state-highlight a,
.ui-widget-header .ui-state-highlight a {
  color: #777620 /*{fcHighlight}*/;
}

.ui-state-error,
.ui-widget-content .ui-state-error,
.ui-widget-header .ui-state-error {
  border: 1px solid @alert-danger-border;
  background: @alert-danger-bg;
  color: @alert-danger-text;
}

.ui-state-error a,
.ui-widget-content .ui-state-error a,
.ui-widget-header .ui-state-error a {
  color: #5f3f3f /*{fcError}*/;
}

.ui-state-error-text,
.ui-widget-content .ui-state-error-text,
.ui-widget-header .ui-state-error-text {
  color: #5f3f3f /*{fcError}*/;
}

.ui-priority-primary,
.ui-widget-content .ui-priority-primary,
.ui-widget-header .ui-priority-primary {
  font-weight: bold;
}

.ui-priority-secondary,
.ui-widget-content .ui-priority-secondary,
.ui-widget-header .ui-priority-secondary {
  opacity: .7;
  font-weight: normal;
}

.ui-state-disabled,
.ui-widget-content .ui-state-disabled,
.ui-widget-header .ui-state-disabled {
  opacity: .35;
  background-image: none;
}

/* Icons
----------------------------------*/

/* states and images */
.ui-icon {
  width: 16px;
  height: 16px;
}

.ui-icon,
.ui-widget-content .ui-icon {
  background-image: url("../images/jquery-ui/ui-icons_444444_256x240.png") /*{iconsContent}*/;
}

.ui-widget-header .ui-icon {
  background-image: url("../images/jquery-ui/ui-icons_444444_256x240.png") /*{iconsHeader}*/;
}

.ui-state-default .ui-icon {
  background-image: url("../images/jquery-ui/ui-icons_777777_256x240.png") /*{iconsDefault}*/;
}

.ui-state-hover .ui-icon,
.ui-state-focus .ui-icon {
  background-image: url("../images/jquery-ui/ui-icons_555555_256x240.png") /*{iconsHover}*/;
}

.ui-state-active .ui-icon {
  background-image: url("../images/jquery-ui/ui-icons_ffffff_256x240.png") /*{iconsActive}*/;
}

.ui-state-highlight .ui-icon {
  background-image: url("../images/jquery-ui/ui-icons_777620_256x240.png") /*{iconsHighlight}*/;
}

.ui-state-error .ui-icon,
.ui-state-error-text .ui-icon {
  background-image: url("../images/jquery-ui/ui-icons_cc0000_256x240.png") /*{iconsError}*/;
}

