.ui-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: block;
  outline: none;

  .ui-menu {
	position: absolute;
  }

  .ui-menu-item {
	margin: 0;
	cursor: pointer;
	/* support: IE10, see #8844 */
	list-style-image: url("data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7");
  }

  .ui-menu-item-wrapper {
	position: relative;
	padding: 3px 1em 3px .4em;
  }

  .ui-menu-divider {
	margin: 5px 0;
	height: 0;
	font-size: 0;
	line-height: 0;
	border-width: 1px 0 0 0;
  }

  .ui-state-focus,
  .ui-state-active {
	margin: -1px;
  }

  .ui-icon {
	position: absolute;
	top: 0;
	bottom: 0;
	left: .2em;
	margin: auto 0;
  }

  .ui-menu-icon {
	left: auto;
	right: 0;
  }
}

/* icon support */
.ui-menu-icons {
  position: relative;

  .ui-menu-item-wrapper {
	padding-left: 2em;
  }
}