.ui-progressbar {
  height: 2em;
  text-align: left;
  overflow: hidden;

  .ui-progressbar-value {
	margin: -1px;
	height: 100%;
  }

  .ui-progressbar-overlay {
	background: url("data:image/gif;base64,");
	height: 100%;
	filter: alpha(opacity=25); /* support: IE8 */
	opacity: 0.25;
  }
}

.ui-progressbar-indeterminate .ui-progressbar-value {
  background-image: none;
}
