.ui-slider {
  position: relative;
  text-align: left;

  .ui-slider-handle {
	position: absolute;
	z-index: 2;
	width: 1.2em;
	height: 1.2em;
	cursor: default;
	-ms-touch-action: none;
	touch-action: none;
  }

  .ui-slider-range {
	position: absolute;
	z-index: 1;
	font-size: .7em;
	display: block;
	border: 0;
	background-position: 0 0;
  }
}

/* support: IE8 - See #6727 */
.ui-slider.ui-state-disabled .ui-slider-handle,
.ui-slider.ui-state-disabled .ui-slider-range {
  filter: inherit;
}

.ui-slider-horizontal {
  height: .8em;

  .ui-slider-handle {
	top: -.3em;
	margin-left: -.6em;
  }

  .ui-slider-range {
	top: 0;
	height: 100%;
  }

  .ui-slider-range-min {
	left: 0;
  }

  .ui-slider-range-max {
	right: 0;
  }
}

.ui-slider-vertical {
  width: .8em;
  height: 100px;

  .ui-slider-handle {
	left: -.3em;
	margin-left: 0;
	margin-bottom: -.6em;
  }

  .ui-slider-range {
	left: 0;
	width: 100%;
  }

  .ui-slider-range-min {
	bottom: 0;
  }

  .ui-slider-range-max {
	top: 0;
  }
}
