.ui-selectmenu-menu {
  padding: 0;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
  display: none;

  .ui-menu {
	overflow: auto;
	overflow-x: hidden;
	padding-bottom: 1px;

	.ui-selectmenu-optgroup {
	  font-size: 1em;
	  font-weight: bold;
	  line-height: 1.5;
	  padding: 2px 0.4em;
	  margin: 0.5em 0 0 0;
	  height: auto;
	  border: 0;
	}
  }
}

.ui-selectmenu-open {
  display: block;
}
.ui-selectmenu-button {
  display: inline-block;
  overflow: hidden;
  position: relative;
  text-decoration: none;
  cursor: pointer;
  width: 14em;

  span {
	&.ui-icon {
	  right: 0.5em;
	  left: auto;
	  margin-top: -8px;
	  position: absolute;
	  top: 50%;
	}

	&.ui-selectmenu-text {
	  text-align: left;
	  padding: 0.4em 2.1em 0.4em 1em;
	  display: block;
	  line-height: 1.4;
	  overflow: hidden;
	  text-overflow: ellipsis;
	  white-space: nowrap;
	}
  }
}